import Matter from "matter-js";
import * as PIXI from "pixi.js";

document.addEventListener("DOMContentLoaded", (event) => {
  /*--------------------------
  Setup
  --------------------------*/

  // Matter Modules
  const Engine = Matter.Engine;
  const World = Matter.World;
  const Body = Matter.Body;
  const Bodies = Matter.Bodies;
  const Mouse = Matter.Mouse;
  const MouseConstraint = Matter.MouseConstraint;

  // Scene Container
  const sceneContainer = document.querySelector(".scene");
  const canvasWidth = sceneContainer.offsetWidth;
  const canvasHeight = sceneContainer.offsetHeight;
  let canvasPrevWidth = canvasWidth;
  let canvasPrevHeight = canvasHeight;

  // Data 
  let sideID = -1;
  let numDrops = [0,0];
  let targetDrops = 0;
  const maxDrops = 250;
  let objects = [];

  // Style
  const backgrounds = ["images/gradient0.jpg","images/gradient1.jpg"];
  const emojiWidth = 35;

  // Init 
  const urlString = window.location.href;
  sideID = urlString.split('?')[1].split('=')[1];
  console.log("SIDE "+sideID);
  targetDrops = localStorage.getItem("num_drops_"+sideID);

  /*--------------------------
  Engine
  ---
  Setup the Matter engine. This is what the Matter bodies will run in.
  --------------------------*/

  const engine = Engine.create();
  engine.gravity.y = 0.8;

  /*--------------------------
  Pixi Data
  --------------------------*/

  const images = [
    {
      src: "images/worried_emoji.png",
      initialPosition: {
        x: 300,
        y: 100
      },
      width: emojiWidth,
      height: emojiWidth
    },
    {
      src: "images/bliss_emoji.png",
      initialPosition: {
        x: 1200,
        y: 100,
      },
      width: emojiWidth,
      height: emojiWidth,
    }
  ];

  /*--------------------------
  Scene Objects
  --------------------------*/

  // Array of objects we want to track in the scene. This will be populated as we create our bodies and sprites from the images.
  const sceneObjects = [];

  /*--------------------------
  Setup Walls
  ---
  Walls will keep our bodies and sprites within a confined area.
  --------------------------*/
/*
  const wallTop = Bodies.rectangle(canvasWidth / 2, 0, canvasWidth, 10, {
    isStatic: true,
  });
   */
  const wallBottom = Bodies.rectangle(
    canvasWidth / 2,
    canvasHeight,
    canvasWidth,
    10,
    {
      isStatic: true,
    }
  );
  const wallRight = Bodies.rectangle(
    canvasWidth,
    canvasHeight / 2,
    10,
    canvasHeight*10,
    {
      isStatic: true,
    }
  );
  const wallLeft = Bodies.rectangle(
    0, 
    canvasHeight / 2, 
    10, 
    canvasHeight*10, 
    {
    isStatic: true,
    }
  );

  // Add Matter walls to the world. This will keep the bodies within certain parameters.
  World.add(engine.world, [wallBottom, wallLeft, wallRight]);

  /*--------------------------
  Pixi
  --------------------------*/

  // Setup Pixi renderer to match the same size as the Matter world.
  const app = new PIXI.Application({
    backgroundAlpha: 0,
    resizeTo: sceneContainer,
  });
  app.stage.interactive = true;
  /*
  app.stage.on('click', function(){
    console.log('hello');
})*/

  // Put the Pixi apps canvas into the scene container.
  document.querySelector(".scene").appendChild(app.view);

  /*--------------------------
  Create Scene Object
  --------------------------*/

  function spawnEmoji(image) {
    // Matter Body
    console.log("CREATE "+image);

    const imageBody = Bodies.circle(
      0.1*window.innerWidth+0.8*window.innerWidth*Math.random(),
      -100,
      image.width/2,
      {
        restitution: 1,
        friction:0.01,
        frictionStatic:0.01,
        slop:0.1
      },
      25
    );
    imageBody.friction = 1;
    World.addBody(engine.world, imageBody);
    
    const imageSprite = new PIXI.Sprite.from(image.src);
    imageSprite.width = image.width;
    imageSprite.height = image.height;
    imageSprite.position;
    imageSprite.anchor.set(0.5, 0.5);
    app.stage.addChild(imageSprite);


    for(let i=0; i<sceneObjects.length; i++){
      sceneObjects[sceneObjects.length-1].body.restitution *= 0.95;
    }


    /*if(sceneObjects.length > 0){
      sceneObjects[sceneObjects.length-1].body.restitution = 0.1;
      //sceneObjects[sceneObjects.length-1].body.friction = 0.01;
      //sceneObjects[sceneObjects.length-1].body.frictionStatic = 0.01;
    }*/
    

    sceneObjects.push({
      body: imageBody,
      sprite: imageSprite,
    });

  }

  /*--------------------------
  Pixi Frame Updates
  --------------------------*/

  app.ticker.add(() => {
    sceneObjects.forEach((object) => {
      // Make all pixi sprites follow the position and rotation of their body.
      object.sprite.position = object.body.position;
      object.sprite.rotation = object.body.angle;
    });
  });

  /*--------------------------
  Mouse Control
  ---
  Add the mouse to the Pixi frame. This is how you enable interaction with the bodies. We aren't using the Matter renderer so need the mouse to be attached to our invisible Matter engine that runs on top of the Pixi world.
  --------------------------*/

  const mouseConstraint = MouseConstraint.create(engine, {
    mouse: Mouse.create(document.querySelector(".scene canvas")),
  });

  World.add(engine.world, mouseConstraint);

  /*--------------------------
  Window Resize
  ---
  Ensure the objects are still bounded to the containers walls even in the case of resizing the browser.
  --------------------------*/

  window.addEventListener("resize", function (event) {
    // Save the new canvas width
    const canvasWidth = sceneContainer.offsetWidth;
    const canvasHeight = sceneContainer.offsetHeight;

    // Reposition all the walls and scale them so they retain their width/height.
    Body.setPosition(wallLeft, {
      x: 0,
      y: canvasHeight / 2,
    });
    Body.scale(wallLeft, 1, canvasHeight / canvasPrevHeight);

    Body.setPosition(wallRight, {
      x: canvasWidth,
      y: canvasHeight / 2,
    });
    Body.scale(wallRight, 1, canvasHeight / canvasPrevHeight);
/*
    Body.setPosition(wallTop, {
      x: canvasWidth / 2,
      y: 0,
    });
    Body.scale(wallTop, canvasWidth / canvasPrevWidth, 1);
 */
    Body.setPosition(wallBottom, {
      x: canvasWidth / 2,
      y: canvasHeight,
    });
    Body.scale(wallBottom, canvasWidth / canvasPrevWidth, 1);

    // Set the new canvas dimensions as the previous. We use this so we can properly scale the environment.
    canvasPrevWidth = canvasWidth;
    canvasPrevHeight = canvasHeight;
  });

  /*--------------------------
  Run
  ---
  This is where we create the initial objects and start the engine.
  --------------------------*/

  function addOne(){
    
    spawnEmoji(images[sideID]);

    numDrops[sideID]++;
    tempText.text = numDrops[sideID];
    localStorage.setItem("num_drops_"+sideID, numDrops[sideID]);
    app.stage.addChild(tempText);

    if(numDrops[sideID] > maxDrops){
      /*let ymove = 1.2;
      Body.translate(wallBottom, {x:0, y:ymove});
      Body.translate(wallLeft, {x:0, y:ymove});
      Body.translate(wallRight, {x:0, y:ymove});
      console.log("v "+numDrops[sideID]);*/

      for(let i=0; i<sceneObjects.length; i++){
        sceneObjects[sceneObjects.length-1].body.slop = Math.min(2,sceneObjects[sceneObjects.length-1].body.slop*3);
      }
      console.log("slop "+sceneObjects[0].body.slop);
    }
  }


  const bgSprite = new PIXI.Sprite.from(backgrounds[sideID]);
  bgSprite.width = window.innerWidth;
  bgSprite.height = window.innerHeight;
  bgSprite.position.set(0,0);
  bgSprite.anchor.set(0,0);
//  bgSprite.onclick = (event) => { alert('clicked!');};
  const bgContainer = new PIXI.Container();
  bgContainer.x = 0;
  bgContainer.y = 0;
  bgContainer.addChild(bgSprite);
  bgContainer.interactive = true;
  bgContainer.on('click', addOne);


  app.stage.addChild(bgContainer);

  const tempText = new PIXI.Text("", {fontSize:64, fontWeight: 'bold'});
  tempText.x = 50;
  tempText.y = 100;
  tempText.text = 0;
  app.stage.addChild(tempText);

 
  for(let i=0; i<targetDrops; i++){
    setTimeout(function(){
      addOne();
    }, i*200);
  }

  document.onkeypress = function (e) {
    console.log(e.keyCode+" | "+sideID);
    if((e.keyCode == 97 && sideID == 0)||(e.keyCode == 108 && sideID==1)){
      addOne();
    }
  };

  // Run the Matter engine. This continuously updates the Matter.Engine. It ensures we can listen for the updates on each tick and move the Pixi objects with Matter bodies (see app.ticker function).
  Matter.Runner.run(engine);
});
